<template>
  <div className="animated">
    <b-card>
      <b-card-header>
        <i class="icon-menu mr-1"></i>Chat Rooms
      </b-card-header>
      <b-card-body>
        <v-client-table :columns="columns" :data="data"  :options="options" :theme="theme" id="dataTable" delay="1500">
          <router-link slot="_id" slot-scope="props" target="_blank" :to="`chatgroups/${props.row._id}`" class="icon-eye"></router-link>
        </v-client-table>
      </b-card-body>
    </b-card>
    <router-link :to="{ name: 'Create Chat Groups' }">
      <b-button type="submit" value="Add Chat Group" variant="outline-dark"> Add Chat Group </b-button>
    </router-link>
  </div>

</template>

<script>
  import Vue from 'vue'
  import {ClientTable, Event} from 'vue-tables-2'
  import axios from 'axios'
  import moment from 'moment';
  Vue.use(ClientTable)
  export default {
    name: 'dashboard',
    components: {
      ClientTable,
      Event
    },
    data: function () {
      return {
        columns: ['room_code','created_date','room_name','total_join_req','no_of_users','_id'],
        data: [],
        joinData:[],
        options: {
          headings: {
            room_code : 'Room code',
            room_name: 'Room Name',
            total_join_req : 'Total Join Request',
            id : 'URL',
          },
          sortable: ['room_code','created_date','room_name','total_join_req','no_of_users'],
          filterable: ['room_code','created_date','room_name','total_join_req','no_of_users'],
          sortIcon: { base:'fa', up:'fa-sort-asc', down:'fa-sort-desc', is:'fa-sort' },
          pagination: {
            chunk: 5,
            edge: false,
            nav: 'scroll'
          }
        },
        useVuex: false,
        theme: 'bootstrap4',
        template: 'default'
      }
    },
    async mounted() {
     await axios.get("https://selacious-cloud-siteapi.herokuapp.com/join")
    .then(
      response => {this.joinData = response.data
        axios.get("https://selacious-cloud-siteapi.herokuapp.com/chats")
        .then(
          resp =>{
            this.data = resp.data
            for(var i=0;i<this.data.length;i++){
              var count =0;
              this.data[i].no_of_users = this.data[i].allowed_users.length
              var day = moment(resp.data[i].created_date,"YYYY-MM-DD ").format("dddd, MMMM Do YYYY, h:mm:ss a");
              this.data[i].created_date = day;
              for(var j=0;j<this.joinData.length;j++){
                if(this.data[i].room_code == this.joinData[j].room_code)
                count = count+1;
              }
              this.data[i].total_join_req = count
            }

          })
    })
    }
  };
// </script>

<style lang="scss">
  #dataTable {
    width: 95%;
    margin: 0 auto;
    .VuePagination {
      text-align: center;
      justify-content: center;
    }
    .vue-title {
      text-align: center ;
      margin-bottom: 10px;
    }
    .VueTables__search-field {
      display: flex;
    }
    .VueTables__search-field input {
      margin-left: 0.25rem;
    }
    .VueTables__limit-field {
      display: flex;
    }
    .VueTables__limit-field select {
      margin-left: 0.25rem !important;
    }
    .VueTables__table th {
      text-align: center;
    }
    .VueTables__child-row-toggler {
      width: 16px;
      height: 16px;
      line-height: 16px;
      display: block;
      margin: auto;
      text-align: center;
    }
    .VueTables__child-row-toggler--closed::before {
      content: "+";
    }
    .VueTables__child-row-toggler--open::before {
      content: "-";
    }
  }
</style>
